import back from '@/components/back'
import noData from '@/components/noData'
export default {
    components: {
        back,
        noData
    },
    data(){
        return {
            list: [],
            Patient: this.$store.state.patient,
            pageShow: false,
            PatientName:''
        }
    },
    computed: {
        title(){
            return `${this.PatientName}的随访计划`
        }
    },
    created(){
        this.getPatient()
        this.pull()
    },
    methods: {
        getPatient(){
           this.$get('/PlatFormAPI/PatientAccount/QueryPatientInfoByPatientId',{
                PatientId: this.Patient.PatientId,
            }).then(o => {
                this.PatientName = o.PatientName
            }) 
        },
        toDetail(id){
            this.$router.push(`/follow-up?id=${id}`)
        },
        pull(){
            this.$post('/PlatFormAPI/Followup/QueryPlanPage',{
                PatientId: this.Patient.PatientId,
                PageIndex: 1,
                PageSize: 100
            }).then(o => {
                this.pageShow = true
                this.list = o.ReturnData || []
            })
        }
    }
}



